<template>
  <div class="navbar">
    <img
      class="menu-icono"
      src="../assets/icons/burger.svg"
      alt="menu icono"
      @click="mostrarmenu = !mostrarmenu"
    />
    <ul class="menu" id="menu-bar" v-if="mostrarmenu">
      <!-- version 1 <li><a href="/#educacion" @click.prevent="scrollear('educacion')">EDUCACIÓN</a></li> -->
      <li>
        <router-link to="/">
          <span @click="delayscroll('quien')">QUIÉN</span>
        </router-link>
      </li>
      <li>
        <router-link to="/">
          <span @click="delayscroll('educacion')">EDUCACIÓN</span>
        </router-link>
      </li>
      <li>
        <router-link to="/trabajos">
          <span @click="mostrar()">
            TRABAJOS
          </span>
        </router-link>
      </li>
      <li>
        <router-link to="/">
          <span @click="delayscroll('experiencia')"
            >EXPERIENCIA</span
          ></router-link
        >
      </li>
      <li>
        <router-link to="/">
          <span @click="delayscroll('conversemos')"
            >CONVERSEMOS!</span
          ></router-link
        >
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "navbar",
  data: function() {
    return {
      mostrarmenu: true,
      anchoVentana: window.innerWidth,
    };
  },
  created() {
    this.mostrar();
  },
  mounted() {
    window.addEventListener("resize", () => {
      this.anchoVentana = window.innerWidth;
      this.mostrar();
    });
  },

  methods: {
    mostrar() {
      if (this.anchoVentana <= 500) {
        this.mostrarmenu = !this.mostrarmenu;
      } else {
        this.mostrarmenu = true;
      }
    },
    scrollear(objeto) {
      let offset;
      let obj = document.getElementById(objeto);
      let objPos = obj.getBoundingClientRect().top;

      if (objeto == "quien") {
        offset == 150;
      } else {
        offset = 80;
      }
      if (window.innerWidth < 1366) {
        offset = 0;
      }
      let scrollTop = window.pageYOffset;
      let scrollOffset = objPos + scrollTop;

      scrollTo({
        top: scrollOffset - offset,
      });
    },
    delayscroll(hash) {
      setTimeout(() => {
        this.mostrar();
        this.scrollear(hash);
      }, 1);
    },
  },
};
</script>

