<template>
  <div class="cv" id="experiencia">
    <h3 class="cv_header" @click ="show3 = !show3">EXPERIENCIA</h3>
    <span class="mostrar" @click ="show3 = !show3">{{show3 ? '●': '—'}}</span>
    <transition name="slide" >
        <div v-if="show3">

            <span class="cv_fechas texto_filtrado">presiona para filtrar</span>
            <div class="box_container" >
                <div class="selector_años">
                    <div>
                        <input type="checkbox" id="2020" name="años" value="2020" v-model="años">
                        <label class="etiquetas" for="2020">2020</label>
                    </div>
                    <div>
                        <input type="checkbox" id="2019" name="años" value="2019" v-model="años">
                        <label class="etiquetas" for="2019">2019</label>
                    </div>
                    <div>
                        <input type="checkbox" id="2018" name="años" value="2018" v-model="años">
                        <label class="etiquetas" for="2018">2018</label>
                    </div>
                    <div>
                        <input type="checkbox" id="2017" name="años" value="2017" v-model="años">
                        <label class="etiquetas" for="2017">2017</label>
                    </div>
                    <div>
                        <input type="checkbox" id="2016" name="años" value="2016" v-model="años">
                        <label class="etiquetas" for="2016">2016</label>
                    </div>
                </div>
                <article class="cv_box" v-for="trabajo in exp" :key="trabajo.id">
                    <div class="titulo_box">
                        <p class="cv_año">{{trabajo.año}}</p>
                        <p class="cv_fechas">{{trabajo.fechas}}</p>
                        <p class="cv_cargo">{{trabajo.cargo}}</p>
                    </div>
                    <p class="cv_descripcion">{{trabajo.descripcion}}</p>

                </article>

            </div>
        </div>
    </transition>
  </div>
</template>

<script>
import experiencia from '../basedatos/experiencia.js'

export default {
  name: 'cv',
  data: function () {
      return {
          exp: [],
          show3: true,
          años: ["2017", "2016", "2018", "2019", "2020"]
      }
  },
  created: function() {
      this.filtrarAño(this.años)
  },
  methods: {
      filtrarAño: function(añosInput) {
          let experienciaArray  = experiencia
          this.exp              = []
          for (let año of añosInput) {
            let filtro          = experienciaArray.filter(x => x.año == año)
            for (let elemento of filtro) {
                this.exp.push(elemento)
                this.exp.sort((a, b) => (a.año < b.año) ? 1 : -1)
            }
          }
         
      }
  },
  watch: {
      años : function () {
          this.filtrarAño(this.años)
      }
  }
  
}
</script>

