<template>
    <div class="chartHabilidades" >
        <canvas id="habilidades-chart"></canvas>
    </div>
</template>

<script>
import Chart from 'chart.js'

export default {
    data: function(){ 
        return{
            anchoVentana: window.innerWidth,
            
        }
    },
    methods: {
        crearChart(in_size) {
            const tamaño = in_size
            const ctx       = document.getElementById('habilidades-chart');
            const myChart   = new Chart(ctx, {
                type: 'radar',
                data: {
                    labels: ['Archicad', 'Rhinoceros', 'Autocad', 'Civil3D', 'QGIS', 'Illustrator', 'Photoshop', 'Indesign', 'VueJs','CSS', 'HTML', 'JS', 'Office', 'Octane Render', 'Cinema 4D', ' Sketchup', 'Revit'],
                    datasets: [
                        {
                            label: 'Dominio de Software',
                            data: [4.5,3.5,5,4,2,4.8,4.6,3.8,3.7,4,4.2,3,5,2,2.5,3.7,2.7],
                            borderWidth:    3,
                            lineTension: 0.5,
                            fill: false,
                            borderColor: '#9dc6d3',
                            pointBackgroundColor: '#4f5d74',
                            pointBorderWidth: 0,
                            pointRadius: 4,
                            pointHoverRadius: 6,
                
                        }
                    ] 
                },
                options: {
                    legend: {
                        display: false
                    },
                    scale: {
                        ticks:{
                            min:1,
                            display: false
                         },
                         gridLines: {
                             borderDash: [2,3],
                             color :'#252525',
                             lineWidth: 0.5
                         },
                         angleLines: {
                             borderDash: [2,3],
                             color :'#252525',
                             lineWidth: 0.5
                         },
                         pointLabels:{
                             fontColor: '#363636',
                             fontSize: tamaño ,
                             fontFamily: "'Epilogue', sans-serif"
                         },
                     },
                     tooltips: {
                         enabled: false
                     }


                }
            })
        myChart
        }
    },
    mounted() {
        this.crearChart(this.tamañoLabel)
        window.addEventListener('resize', () => {
            this.anchoVentana = window.innerWidth
            console.log(this.tamañoLabel)
        })
    },
    watch:{
        tamañoLabel() {
            console.log('change!')
        }
    },
    computed: {
        tamañoLabel() {
            if(this.anchoVentana < 750 ){
                return 10
            } else {
                return 20
            }
        }
    }
}
</script>

<style>

.chartHabilidades {
    position: relative;
    margin: 0 auto;
    width: 80vw;
    max-width: 900px;
}
</style>